@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
.header {
    padding-top: 6rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* ==================== CTA ========================= */
.cta {
    margin-top: 1rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}

/* ==================== HEADER SOCIALS ========================= */
.header__socials {
    margin-top: 1rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}


.nav-header {
    background: var(--color-primary);
    color: var(--color-bg);
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
    height: 80px;
    z-index: 10;
}

.nav-header nav {
    background: var(--color-primary);
    color: var(--color-bg);
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
    height: 80px;
    padding: 0 2rem;
    z-index: 10;
}

nav a {
    color: var(--color-bg);
    margin: 0 2rem;
    text-decoration: none;
    z-index: 10;
}

nav a:hover {
    color: black;
    z-index: 10;
}

.nav-header .nav-btn {
    padding: 5px;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    color: var(--color-bg);
    visibility: hidden;
    opacity: 0;
    z-index: 10;
}
  
  
/* ==================== MEDIA QUERIES (SMALL DEVICES) ========================= */
@media screen and (max-width: 600px) {
    .nav-header .nav-btn {
        visibility: visible;
        opacity: 1;
        z-index: 10;
    }

    .nav-btn-fa-bars {
        margin-left: 1rem;
    }

    .nav-header nav {
        position: fixed;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        grid-gap: 1.5rem;
        gap: 1.5rem;
        background-color: var(--color-primary);
        transition: 1s;
        -webkit-transform: translate(-100vh);
                transform: translate(-100vh);
        z-index: 10;
    }

    .nav-header .reponsive-nav {
        -webkit-transform: none;
                transform: none;
        z-index: 10;
    }
}

::before, ::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  
  .tab-container {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 100%;
    background: #f1f1f1;
    margin: 100px auto 0;
    word-break: break-all;
  }
  
  .bloc-tabs {
    display: flex;
  }

  .tabs {
    padding: 15px;
    text-align: center;
    width: 50%;
    background: transparent;
    cursor: pointer;
    box-sizing: content-box;
    position: relative;
    outline: none;
  }

  /* .tabs:not(:last-child){
    border-right: 1px solid var(--color-primary);
  } */
  
  .active-tabs  {
    background: white;
    border-bottom: 1px solid transparent;
  }
  
  .active-tabs::before {
    content: "";
    display: block;
    position: absolute;
    top: -5px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: calc(100% + 2px);
    height: 5px;
    background: var(--color-primary);
  }
  
  button {
    border: none;
    
  }
  .content-tabs {
    flex-grow : 1;
  }
  .content {
    background: white;
    padding: 20px;
    width: 100%;
    height: 100%;
    display: none;
    word-break: break-word;
  }
  .content h2 {
    padding: 0px 0 5px 0px;
  }
  .content hr {
    width: 100px;
    height: 2px;
    background: #222;
    margin-bottom: 5px;
  }
  .content p {
    width: 100%;
    height: 100%;
  }
  .active-content {
    display: block;
  }

/* ==================== MEDIA QUERIES (MEDIUM DEVICES) ========================= */
@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        grid-gap:0;
        gap:0;
    }

    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p {
        margin: 1rem 0 1.5rem;
    }
  }
  
  /* ==================== MEDIA QUERIES (SMALL DEVICES) ========================= */
  @media screen and (max-width: 600px) {
    .about__me {
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__cards {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }

    .about__content {
        text-align: center;
    }

    .about__content p {
        margin: 1.5rem 0;
    }
  }


.experience__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}

.experience__container > div {
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid var(--color-primary-variant);
    transition: var(--transition);
}

/* .experience__container > div:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
} */

.experience__container > div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.experience__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.experience__details {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.experience__details-icon {
    margin-top: 6px;
    color: var(--color-primary);
}

/* ==================== MEDIA QUERIES (MEDIUM DEVICES) ========================= */
@media screen and (max-width: 1024px) {
    .experience__container {
        grid-template-columns: 1fr;
    }
    
    .experience__container > div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }

    .experience__content {
        padding: 1rem;
    }
  }
  
  /* ==================== MEDIA QUERIES (SMALL DEVICES) ========================= */
  @media screen and (max-width: 600px) {
    .experience__container {
        grid-gap: 1rem;
        gap: 1rem;
    }
    
    .experience__container > div {
        width: 100%;
        padding: 2rem 1rem;
    }
  }
.portfolio__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
}

.portfolio__item {
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 2rem;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
    justify-content: space-between;
    flex-direction: column;
    display: flex;
}

/* .portfolio__item:hover {
    border-color: var(--color-primary-variant);
    background: transparent;
} */

.portfolio__item-image {
    border-radius: 1.5rem;
    overflow: hidden;
}

.portfolio__item h3 {
    margin: 1.2rem 0 2rem;
}

.portfolio__item-cta {
    margin-top: 1rem;
    margin-bottom: 1rem;
    justify-content: flex-start;
    display: flex;
}

/* ==================== MEDIA QUERIES (MEDIUM DEVICES) ========================= */
@media screen and (max-width: 1024px) {
    .portfolio__container {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1.2rem;
        gap: 1.2rem;
    }
}
  
  /* ==================== MEDIA QUERIES (SMALL DEVICES) ========================= */
  @media screen and (max-width: 600px) {
      .portfolio__container {
          grid-template-columns: 1fr;
          grid-gap: 1rem;
          gap: 1rem;
      }
}
.container.contact__container {
    width: 58%;
}

form {
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

input,
textarea {
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var(--color-white);
    font-family: 'Courier New', Courier, monospace;
}

.msg-success {
    color: var(--color-white);
    margin-top: 1rem;
}

.hide-msg-success {
    display: none;
}

/* ==================== MEDIA QUERIES (MEDIUM DEVICES) ========================= */
@media screen and (max-width: 1024px) {
}
  
/* ==================== MEDIA QUERIES (SMALL DEVICES) ========================= */
@media screen and (max-width: 600px) {
    .container.contact__container {
        width: var(--container-width--sm);
    }
}
footer {
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a {
    color: var(--color-bg);
}

.footer__logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer__socials {
    display: flex;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer__socials a {
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}

.footer__socials a:hover {
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footer__copyright {
    color: var(--color-bg);
}
.work-content {
    word-break: break-word;
}
.work-content a{
    color: black;
    text-decoration: underline;
}
.work-content a:hover{
    text-decoration: none;
    transition: opacity 1s ease-in-out;
}
.work-content h2 {
    padding: 0px 0 5px 0px;
}
.work-content hr {
    width: 100px;
    height: 2px;
    background: #222;
    margin-bottom: 5px;
}
.work-content p {
    width: 100%;
    height: 100%;
}
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: #f4eeed;
  --color-bg-variant: #f4eeed;
  --color-primary: #6a938b;
  --color-primary-variant: #3aafa9;
  --color-white: #093426;
  --color-light: #093426;

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width--sm: 90%;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: "Poppins", sans-serif;
  background: #f4eeed;
  background: var(--color-bg);
  color: #093426;
  color: var(--color-white);
  line-height: 1.8;
  /* background-image: url(../src/assets/blue_motion_1.png); */
}

/* ==================== GENERAL STYLES ========================= */
.container {
  width: 75%;
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2, 
h3,
h4,
h5 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
}

section > h2, 
section > h5 {
  text-align: center;
  color: #093426;
  color: var(--color-light);
}

section > h2 {
  color: #6a938b;
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light {
  color: #093426;
  color: var(--color-light);
}

a {
  color: #6a938b;
  color: var(--color-primary);
  transition: all 400ms ease;
  transition: var(--transition);
}

a:hover {
  color: #093426;
  color: var(--color-white);
}

.btn {
  width: -webkit-max-content;
  width: max-content;
  display: inline-block;
  color: #6a938b;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid #6a938b;
  border: 1px solid var(--color-primary);
  transition:all 400ms ease;
  transition:var(--transition) ;
}

.btn:hover {
  background:#093426;
  background:var(--color-white);
  color: #f4eeed;
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  background: #6a938b;
  background: var(--color-primary);
  color: #f4eeed;
  color: var(--color-bg);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* ==================== MEDIA QUERIES (SMALL DEVICES) ========================= */
@media screen and (max-width: 600px) {
  .permalinks {
    flex-direction: column;
    grid-gap: 1.5rem;
    gap: 1.5rem;
  }

  .footer__socials {
    margin-bottom: 2.6rem;
  }
}
